






































































































































import { Component, Emit, Vue } from "vue-property-decorator";
import FileUploadButton from "@/components/common/utils/button/FileUploadButton.vue";
import { ins } from "@/lib/axios";
import { IBoard } from "@/interfaces/IBoard";

@Component({
  components: {
    FileUploadButton,
  },
})
export default class DottoPostingComponent extends Vue {
  dottoPosting = "dottoPosting";

  title = "";
  content = "";
  originalPrice = 0;
  price = 0;
  salesPrice = 0;
  salesYn = false;
  genreDefault = "";
  totalTimeDefault = "";
  postPhoto: any = [];
  tag: string[] = [];
  addTag = "";
  genre: IBoard.SelectOptions[] = [
    { text: "올드스쿨", value: "1" },
    { text: "뉴스쿨", value: "2" },
    { text: "재패니즈", value: "3" },
    { text: "블랙엔그레이", value: "4" },
    { text: "다크사이드", value: "5" },
    { text: "트래쉬폴카", value: "6" },
    { text: "치카노", value: "7" },
    { text: "블랙워크", value: "8" },
    { text: "라인워크", value: "9" },
    { text: "폴리네시안", value: "10" },
    { text: "컬러", value: "11" },
    { text: "커스텀", value: "12" },
  ];
  totalTime: IBoard.SelectOptions[] = [
    { text: "1 시간 이내", value: "1" },
    { text: "1 ~ 2 시간", value: "2" },
    { text: "2 ~ 3 시간", value: "3" },
    { text: "4 시간 이상", value: "4" },
  ];
  openBtn = true;
  activeBtn = "active-post-btn";

  created(): void {
    this.changeBackground();
  }

  getImg(img: IBoard.IFileUpload[]) {
    img.forEach((value) => {
      const { file } = value;
      console.log(file)
      this.postPhoto.push(file);
    });
    console.log(this.postPhoto)
  }

  private validation(): void {
    this.openBtn = !(
      this.title.length &&
      this.genreDefault.length &&
      this.totalTimeDefault.length &&
      this.content.length &&
      this.originalPrice
    );
  }

  async posting(): Promise<void> {
    const headers = {
      "Content-Type": "multipart/form-data",
    };
    const formData = new FormData();
    formData.append("title", this.title);
    formData.append("content", this.content);
    formData.append("price", String(this.price));
    formData.append("salesPrice", String(this.salesPrice));
    formData.append("salesYn", this.salesYn ? "Y" : "N");
    formData.append("genre", this.genreDefault);
    formData.append("totalTime", this.totalTimeDefault);
    formData.append("tags", String(this.tag));

    if (this.postPhoto.length > 0) {
      for (const file of this.postPhoto) {
        formData.append('postPhoto', file);
      }
    }
    const { data } = await ins.post("/dottopost", formData, { headers });
    console.log(data);
    if (data) {
      await this.$router.push("/");
    }
  }

  private addTags(): void {
    if (this.tag.length < 5) {
      this.tag.push(this.addTag);
      this.addTag = "";
    } else {
      alert("모달 새로만들어서 최대 태그 문구 화면에 출력");
      this.addTag = "";
    }
  }

  private deleteTag(index: number): void {
    this.tag.splice(index, 1);
  }

  @Emit("changeBackground")
  private changeBackground() {
    return "posting";
  }
}
